<template>
<div id="wrapper" class=" wrapper-left-fixed wrapper-header-fixed">
    <div class="d-sm-flex justify-content-sm-center d-block">
        <div class="card card-light p-0 my-sm-4 my-0">
            <div class="box-account">
                <img class="box-account-logo" src="/static/img/logo/talk-logo.jpg"/>
                <h6 class="box-account-title">Provider Login </h6>
                <b-form class="box-account-form" @submit="onSubmit" @reset="onReset">
                    <span class="reauth-email"> </span>
                    <b-form-input v-model="form.email" type="text" required placeholder="Email"></b-form-input>
                    <b-form-input v-model="form.password" type="password" required placeholder="Password"></b-form-input>
                    <b-button class="btn btn-danger btn-lg btn-block btn-wave-light" type="submit">Login</b-button>
                </b-form>
            </div>
        </div>
    </div>
    <loading :active.sync="isLoading"
        :can-cancel="false"
        :is-full-page="true"></loading>
</div>
</template>

<script>
import api from '@/services/api'
import database from '@/services/store'
import service from '@/services/service'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
export default {
  name: 'Login',
  data () {
    return {
      isLoading:false,
      form:{
        email:'',
        password:''
      }
    }
  },
  components: {
    Loading
  },
  created:function(){
    database.isLogined(()=>{
      this.$router.push({path:'/main/0'});
    });
  },
  methods:{
    onSubmit(e){
      e.preventDefault();
      this.isLoading = true;
      api.api_post('/users/signin','', this.form, (resp)=>{
        if (JSON.stringify(resp.provider) === '{}'){
            alert('Not Provider');
            return;
        }
        database.userLogined(this.form.email, resp.token);
        api.api_get('/users/subscribe/' + resp.id + '/plan', resp.token, r=>{
            service.savePlans(r);
        }, err=>{
            alert('Load purchase plan faild!');
        })
        service.updateTags(_=>{
          this.isLoading = false;
            service.saveUserId(resp.id);
            service.saveProfile(resp);
            this.$router.push('/main/0');
        })
      }, (err)=>{
        this.isLoading = false;
        console.log(err);
        alert(err);
      })
    },
    onReset(e){
      e.preventDefault();
      this.form.email = '';
      this.form.password = '';
    }
  }
}
</script>



