<template>
    <Dashbord>
        <b-breadcrumb>
            <b-breadcrumb-item text="Home" to='/main/0'></b-breadcrumb-item>
            <b-breadcrumb-item :text="past_tag" @click="tag_clicked"></b-breadcrumb-item>

        </b-breadcrumb>
        <div class="row">
            <div class="col-2"></div>
            <div class="col-8">
                <div>
                    <b-form-group
                        id="title"
                        description="The Content Title."
                        label="Title"
                        label-for="title_input"
                        :invalid-feedback="invalidTitle"
                        :valid-feedback="validTitle"
                        :state="titleState"
                        >
                        <b-form-input id="title_input" v-model="content.title" :state="titleState" trim></b-form-input>
                    </b-form-group>
                </div>
            </div>
            <div class="col-2"></div>
        </div>
        <div class="row">
            <div class="col-2"></div>
            <div class="col-3">Thumbnail:
                <input type="file" id="upload-thumbnail" multiple @change="thumbFileChanged"/>
            </div>
            <div class="col-1"></div>
            <div class="col-2">Film:
                <input type="file" id="upload-mp4" multiple @change="mp4FileChanged"/>
            </div>
            <div class="col-2"></div>
        </div>
        <div class="row">
            <div class="col-2"></div>
            <div class="col-3">
                <b-img thumbnail fluid :src="content.thumbnail" alt="Thumbnail"></b-img>
            </div>
            <div class="col-1"></div>
            <div class="col-3">
                <video controls ref="videoRef" :src="preview_url" id="video-container" width="360px"
                ></video>
            </div>
            <div class="col-2"></div>
        </div>
        <div class="row">
            <div class="col-2"></div>
            <div class="col-8">
                <h6>Subtitle</h6>
                <b-form-textarea
                id="textarea"
                v-model="content.contents.temp_txt"
                placeholder="Enter something..."
                rows="2"
                max-rows="4"
                ></b-form-textarea>
            </div>
            <div class="col-2"></div>
        </div>
        <div class="row">
            <div class="col-5"></div>
            <div class="col-2">
                <br/>
                <b-button variant="primary" @click="doSubmit">Submit</b-button>
            </div>
            <div class="col-5"></div>
        </div>
        <loading :active.sync="isLoading"
        :can-cancel="false"
        :is-full-page="true"></loading>
    </Dashbord>
</template>
<script>
import api from '@/services/api'
import database from '@/services/store'
import service from '@/services/service'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import { EventBus } from '@/services/event-bus.js';
import Dashbord from './Dashbord.vue'
export default {
  name: 'Login',
  computed: {
      titleState() {
        return this.content.title.length >= 4 ? true : false
      },
      invalidTitle() {
        if (this.content.title.length > 4) {
          return ''
        } else if (this.content.title.length > 0) {
          return 'Enter at least 4 characters'
        } else {
          return 'Please enter something'
        }
      }
    },
  data () {
      return {
          validTitle:'',
          isLoading:false,
          past_tag:'',
          past_tid:0,
          past_pid:1,
          editStatus:0,
          preview_url:'',
          content:{
            id:0,
            lang:'en',
            doc_type:'doc',
            title:'',
            thumbnail:'',
            www_url:'',
            tar_langs:'',
            price: 0,
            is_original: 1,
            resource_type: 'pay',
            keywords:'',
            tag: 0,
            contents:{
                media_local_path:"",
                media_url:"",
                subtitles:[
                ],
                temp_txt:''
            }
          },
      }

  },
  components: {
    Loading,
    Dashbord
  },
  created(){
    let tid = this.$route.params.tid;
    service.tagById(tid, tag=>{
        this.past_tag = tag.name;
        let content = service.loadContent()
        //console.log("will edit content:", content);
        this.content.tag = tid;
        if (content.id>0){
            this.content.id = content.id,
            this.editStatus = 1;
            this.content.title = content.title;
            this.content.thumbnail = content.thumbnail;
            this.content.www_url = content.www_url;
            this.content.tar_langs = content.tar_langs;
            this.content.contents = JSON.parse(content.contents);
            if (this.content.contents.temp_txt === undefined){
                this.content.contents.temp_txt = '';
            }
            let bucket = service.bucketInUrl(content.www_url);
            service.uploadEnv(bucket, (client, _)=>{
                console.log('insigned:', content.www_url)
                let fileKey = service.getFileKey(bucket, content.www_url)
                console.log('file key:', fileKey);
                this.preview_url = client.signatureUrl(fileKey);
                console.log('signed:', this.preview_url);
            }, err=>{})
        }else{
            this.editStatus = 0;
        }
    });
  },
  methods:{
      tag_clicked(e){
          this.$router.push('/main/' + this.past_pid);
      },
      thumbFileChanged(e){
          this.isLoading = true;
          let file = e.target.files[0];
          if (file.size > 1024 * 300){
              this.isLoading = false;
              alert('Warning:Thumbnail file size > 300K! Won\'t upload!');
              return;
          }
          let fileKey = service.thumbnailFilekey(file.name)
          service.uploadEnv('test-atk', (client, _)=>{
              client.put(fileKey, file).then(r=>{
                  this.isLoading = false;
                  this.content.thumbnail = r.url;
              });
          }, err=>{

          });
      },
      mp4FileChanged(e){
          this.isLoading = true;
          let file = e.target.files[0];
          if (file.size > 1024 * 6000){
              this.isLoading = false;
              alert('Warning:Film file size > 6M! Won\'t upload!');
              return;
          }
          service.uploadEnv('atk-temp-mov', (client, email)=>{
              let fileKey = service.movieFilekey(email, file.name);
              client.put(fileKey, file).then(r=>{
                  this.isLoading = false;
                  this.content.www_url = r.url;
                  this.content.contents.media_url = r.url
                  this.preview_url = client.signatureUrl(fileKey, {
                      expires: 10,
                      method: 'GET'});
                  console.log('preview:', this.preview_url);
              });
          }, err=>{

          });
      },
      doSubmit(){
          if (!this.titleState){
              return;
          }
          if (this.content.thumbnail.indexOf('http')<0){
              alert('Warning:Thumbnail not uploaded! Last error was file size > 300K');
              return;
          }
          if (this.content.www_url.indexOf('http')<0){
              alert('Warning:Film not uploaded! Last error was file size > 3M');
              return;
          }
          this.isLoading = true;
          service.updateContent(this.content, _=>{
              this.isLoading = false;
              this.$router.push('/main/' + this.past_tid);
          });
      }
  }
}
</script>




