/*jshint esversion: 6 */

import api from './api';
import database from './store';
const uuid = require('uuidv4');

export default{
    updateTags(onTags){
        database.loginedToken((email, token)=>{
            api.api_get('/user_contents/tags', token, resp=>{
                //console.log(resp.tags);
                database.updateTags(resp.tags, _=>{
                    onTags();
                });
            }, error=>{

            });
        });
    },
    userLogout(onLogout){
        database.logout(_=>{
            onLogout();
        });
    },
    postList(tag_id, pid, onResult, onError){
        database.loginedToken((email, token)=>{
            if (tag_id < 1){
                api.api_get('/user_contents/content/mine2?p=' + pid, token, resp=>{
                    onResult(resp.contents, resp.item_count);
                }, (code, error)=>{
                    onError(error);
                });
            }else{
                api.api_get('/user_contents/content/'+ tag_id +'/mine3?p=' + pid, token, resp=>{
                    onResult(resp.contents, resp.item_count);
                }, (code, error)=>{
                    onError(error);
                });
            }
        });
    },
    tagById(tagId, onTag){
        database.scope(tx=>{
            database.select(tx,'SELECT * FROM Tags WHERE id=?', [parseInt(tagId)], result=>{
                if(result.rows.length>0){
                    onTag(result.rows[0]);
                }
            });
        });
    },
    pushContent(content){
        window.localStorage['current_doc'] = JSON.stringify(content);
    },
    loadContent(){
        return JSON.parse(window.localStorage['current_doc']);
    },
    savePlans(plans){
        window.localStorage['purchasePlans'] = JSON.stringify(plans);
    },
    loadPlans(){
        return JSON.parse(window.localStorage['purchasePlans']);
    },
    saveUserId(uid){
        window.localStorage['CurrentUserId'] = uid.toString();
    },
    currentUserId(){
        return parseInt(window.localStorage['CurrentUserId']);
    },
    saveProfile(profile){
        window.localStorage['UserProfile'] = JSON.stringify(profile);
    },
    getProfile(){
        return JSON.parse(window.localStorage['UserProfile']);
    },
    uploadEnv(bucket, doUpoad, onErr){
        database.loginedToken((email, token)=>{
            console.log('current token:', token);
            api.api_get('/users/oss/sts/token?token='+token,'', (result)=>{
              console.log('sTS:', result);
              let client = new OSS({
                accessKeyId: result.AccessKeyId,
                accessKeySecret: result.AccessKeySecret,
                stsToken: result.SecurityToken,
                endpoint: 'oss-cn-hongkong.aliyuncs.com',
                bucket: bucket
              });
              doUpoad(client, email);
            }, (err)=>{
              console.log('sTS error:', err);
              onErr(err);
            });
          });
    },
    uuidv4(){
        return uuid();
    },
    thumbnailFilekey(file_name){
        let temp_slice = file_name.split('.');
        let ext = temp_slice[temp_slice.length - 1];
        return 'thumbnail/' + uuid() + "." + ext;
    },
    movieFilekey(email, file_name){
        return email + "/" + file_name;
    },
    bucketInUrl(url){
        return url.split('/')[2].split('.')[0];
    },
    getFileKey(bucket, url){
        let seqs = url.split('/');
        let filename = seqs[3];
        if (bucket === 'atk-mp4'){
            return filename;
        }else{
            var the_email = seqs[3].replace('%40', '@');
            return the_email + "/" + seqs[4];
        }
    },
    updateContent(content, onSuccess){
        content.contents = JSON.stringify(content.contents);
        console.log('will post:', content);
        database.loginedToken((email, token)=>{
            if (content.id>0){
                api.api_post('/user_contents/content/'+ content.id + '/backend/update', token, content, r=>{
                    onSuccess();
                }, err=>{
                    alert(err);
                });
            }else{
                api.api_post('/user_contents/content/backend', token, content, r=>{
                    onSuccess();
                }, err=>{
                    alert(err);
                });
            }
        });
    },
    updatePurchasePlans(currentUid, plans, onSuccess){
        database.loginedToken((_, token)=>{
            api.api_post('/users/subscribe/' + currentUid + '/plan', token, plans, r=>{
                onSuccess(r.plans);
            }, err=>{
                alert(err);
            });
        });
    },
    updateIcon(url, onSuccess){
        database.loginedToken((_, token)=>{
            api.api_post('/users/icon', token, {icon:url}, r=>{
                onSuccess();
            }, (code, err)=>{
                alert(err);
            });
        });
    },
    updateUserprofile(profile, onSuccess){
        let param = {
            lang: profile.lang,
            name: profile.name,
            icon: profile.icon
        };
        database.loginedToken((_, token)=>{
            console.log('will profile:', param);
            api.api_post('/users/profile', token, param, _=>{
                onSuccess();
            }, (code, err)=>{
                alert(err);
            })
        });
    }
};



