/*jshint esversion: 6 */

let db = openDatabase('mydb', '1.0', 'uploaderDB', 2 * 1024 * 1024);
db.transaction(function (tx) {

  //tx.executeSql('DROP TABLE IF EXISTS Logined');
  //tx.executeSql('DROP TABLE IF EXISTS Tags');

  tx.executeSql('CREATE TABLE IF NOT EXISTS Logined (email unique, accesstoken)');
  tx.executeSql('CREATE TABLE IF NOT EXISTS Tags (id unique, name, code, isFree)');
});

function selectByKey(email, onItem){
  db.transaction(function (tx) {
    tx.executeSql('SELECT * FROM Logined WHERE email=?', [email], function (tx, results) {
       var len = results.rows.length, i;
       if (len>0){
         onItem(tx, results.rows[0]);
       }else{
         onItem(tx, null);
       }
    }, (tx, err)=>{
      console.log(err);
    });
  });
}

function scope(funcs){
    db.transaction(tx=>{
        funcs(tx);
    });
}

function select(tx, sql, params, onResult){
  console.log('SQL:', sql, params);
  tx.executeSql(sql, params, function (tx, results) {
    onResult(results);
  }, (tx, err)=>{
    console.log("select err:", err);
  });
}

function exec(tx, sql, params, onSuccess){
  tx.executeSql(sql, params, function(tx, result){
    if (onSuccess!==undefined){
        onSuccess();
    }
  }, (tx, error)=>{
      console.log('sql error:', error);
  });
}

function doTask(idx, tasks, onSuccess){
    console.log('run task:', idx);
    let task = tasks[idx];
    task(_=>{
        if(idx<tasks.length-1){
            let newIdx = idx + 1;
            doTask(newIdx, tasks, onSuccess);
        }else{
            onSuccess();
        }
    });
}

function queued(tasks, onSuccess){
    doTask(0, tasks, onSuccess);
}


export default{
  userLogined(email, token){
    selectByKey(email, (tx, item)=>{
      //console.log('get:', item);
      if (item==null){
        exec(tx, 'INSERT INTO Logined(email, accesstoken) VALUES (?,?)', [email, token]);
      }else{
        exec(tx, 'UPDATE Logined SET accesstoken=? WHERE email=?', [token, email]);
      }
    });
  },
  isLogined(onLogined){
    db.transaction(function (tx) {
      tx.executeSql('SELECT * FROM Logined', [], (tx, result)=>{
        if (result.rows.length>0){
          onLogined();
        }
      });
    });
  },
  loginedToken(onToken){
    db.transaction(function (tx) {
      tx.executeSql('SELECT * FROM Logined', [], (tx, result)=>{
        if (result.rows.length>0){
          onToken(result.rows[0].email, result.rows[0].accesstoken);
        }
      });
    });
  },
  fileExists(file_name, ifNotExist){
    db.transaction(function(tx){
      select(tx, 'SELECT * FROM Files WHERE filename=?', [file_name], (resules)=>{
        //console.log("f results:", resules);
        if (resules.rows.length < 1){
          //console.log('fire exists');
          ifNotExist();
        }
      });
    });
  },
  insertFile(file_name, ext, uri){
    db.transaction(function(tx){
      let ds = new Date().toLocaleDateString();
      tx.executeSql('INSERT INTO Files (filename, ext, uri, uploadDatetime)VALUES (?,?,?,?)', [file_name, ext, uri, ds]);
    });
  },
  uploadedFiles(onResults){
    db.transaction(function(tx){
      select(tx, 'SELECT * FROM Files ORDER By filename ASC', [], (results)=>{
        onResults(results);
      });
    });
  },
  updateTags(tags, onSuccess){
    scope(tx=>{
        exec(tx, 'DELETE FROM Tags', [], _=>{
            var success = 0;
            for(var tag of tags){
                exec(tx, 'INSERT INTO Tags (id, name, code, isFree) VALUES (?,?,?,?)', [tag.id, tag.name, tag.code, tag.is_free], _=>{
                    success++;
                    if(success === tags.length){
                        onSuccess();
                    }
                });
            }

        });
    });
  },
  logout(onSuccess){
      scope(tx=>{
          exec(tx, 'DELETE FROM Logined', [], _=>{
              onSuccess();
          });
      });
  },
  tagsAll(onTags){
      scope(tx=>{
        select(tx, 'SELECT * FROM Tags ORDER BY id ASC', [], results=>{
            onTags(results.rows);
        });
      });
  },
  scope:scope,
  select:select,
  exec:exec
}



