<template>
  <Dashbord>
      <br>
      <br>
      <div class="row">
          <div class="col-4"></div>
          <div class="col-4">
              <template v-for="plan in plans">
                <b-form :key="plan.name" inline>
                    <label>{{plan.name}}</label>

                    <label class="sr-only" for="inlineFormInputGroupUsername2">Price</label>
                    <b-input-group prepend="$" class="col-md-5">
                    <b-input id="inlineFormInputGroupUsername2" placeholder="Username" v-model="plan.price"/>
                    </b-input-group>

                    <b-button variant="primary" @click="update_user_plan">Update</b-button>
                </b-form>
              </template>
          </div>
          <div class="col-4"></div>
      </div>
      <loading :active.sync="isLoading"
        :can-cancel="false"
        :is-full-page="true"></loading>
  </Dashbord>
</template>
<script>
import Dashbord from './Dashbord.vue'
import api from '@/services/api'
import service from '@/services/service'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
export default{
  name: 'Plans',
  components:{ Dashbord, Loading },
  created(){
      this.loadPlans();
  },
  data(){
    return {
        isLoading:false,
        plans:[]
    }
  },
  methods:{
      loadPlans(){
          this.plans = service.loadPlans().plans;
      },
      update_user_plan(){
          console.log("edited plans:", this.plans);
          let currentUid = service.currentUserId();
          service.updatePurchasePlans(currentUid, this.plans, r=>{
              alert('Purchase plan updated!');
              service.savePlans({plans: r});
              this.plans = r;
          });
      }
  }
}
</script>



