<template>
  <Dashbord>
    <div class="row">
      <div class="col-2"><Chns></Chns></div>
      <div class="col-10"><Posts></Posts></div>
    </div>
  </Dashbord>
</template>
<script>
import Dashbord from './Dashbord.vue'
import Posts from './Posts.vue'
import Chns from './Chns.vue'
import { EventBus } from '@/services/event-bus.js';
export default{
  name: 'Content',
  components:{ Dashbord, Chns, Posts },
  created(){
      let tid = this.$route.params.tid;
      setTimeout(_=>{EventBus.$emit('reload_posts', tid, this.current_pid);}, 500);
  },
  data(){
    return {
        current_tag:0,
        current_pid:1
    }
  }
}
</script>



