<template>
  <b-navbar toggleable="lg" type="dark" variant="info">
    <b-navbar-brand href="#">ATalk</b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <b-nav-item href="#" @click="contentClick">Content Manage</b-nav-item>
        <b-nav-item href="#" @click="plansClick">Purchse Plan</b-nav-item>
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown right>
          <!-- Using 'button-content' slot -->
          <template slot="button-content"><em>User</em></template>
          <b-dropdown-item to="/profile">Profile</b-dropdown-item>
          <b-dropdown-item href="javascript:void(0);" @click="exitClicked">Sign Out</b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>

<script>
import service from '@/services/service'
import { EventBus } from '@/services/event-bus.js';
export default{
  name: 'Nav',
  data () {
    return {
    }
  },
  created(){
      console.log('router:', this.$router.history.current.name);
      EventBus.$on('logout', () => {
        service.userLogout(_=>{
              this.$router.push('/');
          });
      });
  },
  methods: {
      exitClicked(){
          service.userLogout(_=>{
              this.$router.push('/');
          });
      },
      plansClick(){
          this.$router.push('/plans');
      },
      contentClick(){
          this.$router.push('/main/0');
      }
  }
}
</script>



