<template>
  <Dashbord>
      <br>
      <br>
      <div class="row">
          <div class="col-2"></div>
          <div class="col-3">
              <b-img thumbnail fluid :src="icon_preview" alt="Image 1" @click="showFileDialog"></b-img>
              <input style="display:none;" type="file" ref="uploadIcon" id="uploadIcon" multiple @change="thumbFileChanged"/>
          </div>
          <div class="col-5">
            <b-form-group
                id="user_name"
                description="Show name of user."
                label="Name"
                label-for="user_name_input"
                :invalid-feedback="invalidUsername"
                :valid-feedback="validUsername"
                :state="state"
                >
                <b-form-input id="user_name_input" v-model="profile.name" :state="state" trim></b-form-input>
            </b-form-group>
            <b-form-group label="Default Language">
                <b-form-radio-group
                    id="default_language"
                    v-model="profile.lang"
                    :options="language_options"
                    name="default_language"
                ></b-form-radio-group>
            </b-form-group>
            <b-form-group label="">
                <b-button variant="outline-primary" @click="updateProfile">Update</b-button>
            </b-form-group>
          </div>
          <div class="col-2"></div>
      </div>
      <loading :active.sync="isLoading"
        :can-cancel="false"
        :is-full-page="true"></loading>
  </Dashbord>
</template>
<script>
import Dashbord from './Dashbord.vue'
import service from '@/services/service'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
export default {
    name: 'Plans',
    components:{ Dashbord, Loading },
    created(){
        this.loadProfile();
    },
    computed:{
        state(){
            return this.profile.name.length >= 2 ? true : false
        },
        invalidUsername(){
            if (this.profile.name.length > 4) {
                return ''
            } else if (this.profile.name.length > 0) {
                return 'Enter at least 2 characters'
            } else {
                return 'Please enter something'
            }
        }
    },
    data(){
        return {
            validUsername:'',
            isLoading:false,
            profile:{},
            icon_preview:'/static/img/profile/unset.jpg',
            language_options:[
                { text: '🇺🇸', value: 'en' },
                { text: '🇯🇵', value: 'jp' }
            ]
        }
    },
    methods:{
        loadProfile(){
            this.profile = service.getProfile();
            console.log('my profile:', this.profile);
            if (this.profile.icon.length>10){
                this.icon_preview = this.profile.icon;
            }
        },
        showFileDialog(){
            this.$refs.uploadIcon.click();
        },
        thumbFileChanged(e){
            this.isLoading = true;
            let file = e.target.files[0];
            service.uploadEnv('test-atk', (client, email)=>{
                let fileKey = service.thumbnailFilekey(file.name);
                client.put(fileKey, file).then(r=>{
                  this.isLoading = false;
                  this.profile.icon = r.url;
                  this.icon_preview = r.url;
                  service.updateIcon(r.url, _=>{
                      alert('Icon updated!');
                  });
              }, err=>{

              });
            });

        },
        updateProfile(){
            if (!this.state){
                return;
            }
            this.isLoading = true;
            service.updateUserprofile(this.profile, _=>{
                this.isLoading = false;
                alert('User profile updated!');
            })
        }
    }

}
</script>



