<template>
<div>
    <loading :active.sync="isLoading"
        :can-cancel="true"
        :on-cancel="onCancel"
        :is-full-page="fullPage"></loading>
    <br/>
    <div>
        <b-nav>
            <b-nav-item active @click="newPostClick">
                <font-awesome-icon :icon="['fas', 'plus']" />
            </b-nav-item>
            <b-nav-item disabled>{{ tag }}</b-nav-item>
        </b-nav>
    </div>
    <b-table :items="posts" :fields="fields">
        <template slot="title" slot-scope="data">
          <template v-if="data.item.on_publish=='Published'">
            {{ data.item.title }}
          </template>
          <template v-else>
            <a href="javascript:void(0);" @click="toEditContent(data.item)"><font-awesome-icon :icon="['fas', 'edit']" />&nbsp;{{ data.item.title }}</a>
          </template>
        </template>
    </b-table>
    <div class="overflow-auto">
    <b-pagination
      v-model="pid"
      :total-rows="item_count"
      :per-page="20"
      @change="change"
    ></b-pagination>
  </div>
</div>
</template>
<script>
import { EventBus } from '@/services/event-bus.js';
import service from '@/services/service.js'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import utils from '@/services/utils.js';
export default{
  name: 'Posts',
  components: {
            Loading
  },
  data(){
    return {
        contents:{},
        isLoading: false,
        fullPage: true,
        page_count: 0,
        posts:[],
        item_count:0,
        tag_id:0,
        tag:'',
        pid:1,
        fields:[
            {
                key: 'title',
                label: 'Title',
                sortable: true
            },
            {
                key: 'tag',
                label:'Tag',
                sortable: true
            },
            {
                key: 'on_publish',
                label: 'Publish Status',
                sortable: true
            },
            {
                key: 'create_time',
                label: 'Create Time',
                sortable: true
            }

        ]
    }
  },
  created(){
    EventBus.$on('reload_posts', (tid, idx) => {
        this.tag_id = tid;
        if (tid===0){
            this.tag = 'All Content'
        }else{
            service.tagById(tid, tag=>{
                this.tag = tag.name;
            });
        }
        this.pid = idx;
        console.log('reload-posts:', tid, idx);
        this.loadContens();
    });
  },
  methods:{
      onCancel(){},
      loadContens(){
        let STATUS = {
            0: 'Not Publish',
            1: 'Published'
        };
        this.isLoading = true;
        service.postList(this.tag_id, this.pid, (contents, item_count)=>{
            this.item_count = item_count
            this.posts = [];
            for (let content of contents){
                this.posts.push({
                    id: content.id,
                    title: content.title,
                    on_publish: STATUS[content.on_publish],
                    tag: content.tags,
                    tag_id: content.tag,
                    create_time: utils.formatDate(content.create_time)
                })
                this.contents[content.id] = content;
            }
            this.isLoading = false;
        }, (error)=>{
            this.isLoading = false;
            alert(error);
        });
      },
      change(pageNum){
        console.log("press:", pageNum);
        this.pid = pageNum;
        this.loadContens();
      },
      toEditContent(content){
          console.log("click:", content);
          service.pushContent(this.contents[content.id]);
          this.$router.push('/editor/' + content.tag_id);
      },
      newPostClick(){
          if (this.tag_id<1){
              alert("Choose a tag");
          }else{
              service.pushContent({id:0})
              this.$router.push('/editor/' + this.tag_id);
          }
      }
  }
}
</script>



