<template>
    <b-list-group>
        <b-list-group-item :key="0">
            <a href="javascript:void(0);" @click="changeTag(0)">
            All Content
            </a>
        </b-list-group-item>
        <b-list-group-item :key="tag.id" v-for="tag in tags">
            <a href="javascript:void(0);" @click="changeTag(tag.id)">
            {{ tag.name }}
            </a>
        </b-list-group-item>
    </b-list-group>


</template>
<script>
import database from '@/services/store'
import { EventBus } from '@/services/event-bus.js';
export default{
  name: 'Chns',
  data(){
    return {
        tags:[]
    }
  },
  created:function(){
      console.log('will load tags from database');
      this.loadTags();
  },
  methods:{
      loadTags(){
          this.tags = [];
          database.tagsAll(tags=>{
              for (let tag of tags){
                  //console.log('tag:', tag);
                  this.tags.push({
                      id:tag.id,
                      name:tag.name
                  });
              }
          })
      },
      changeTag(tid){
          EventBus.$emit('reload_posts', tid, 1);
      }
  }
}
</script>



