/*jshint esversion: 6 */
import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Content from '@/components/Content'
import Plans from '@/components/Plans'
import Editor from '@/components/Editor'
import Profile from '@/components/Profile'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/main/:tid',
      name: 'Content',
      component: Content
    },
    {
      path: '/plans',
      name: 'Plans',
      component: Plans
    },
    {
        path: '/editor/:tid',
        name: 'Editor',
        component: Editor
    },
    {
        path: '/profile',
        name: 'Profile',
        component: Profile
    }
  ]
})



